/* google font */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800&family=VT323&display=swap');

/* layout */
:root {
  --primary: #1aac83;
  --error: #e7195a;
}
body {
  background-image: url("https://images.unsplash.com/photo-1579952363873-27f3bade9f55?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8Zm9vdGJhbGx8ZW58MHx8MHx8&auto=format&fit=crop&w=800&q=60");
  /* background-color: rgb(0, 0, 0); */
  background-repeat: no-repeat;
  background-size: 100%;
  height: 70vh;
  margin: 0;
  font-family: "Arial";
  overflow-x: hidden;
}
header {
  background: rgb(0, 0, 0);
}
header .container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
nav h3{
  padding: 2rem;
}
header a {
  color: rgb(255, 255, 255);
  text-decoration: none;
}
.pages{
  max-width: 1400px;
  padding: 20px;
  margin: 0 auto;
}

/* homepage */
.home {
  display: grid;
  grid-template-columns: 3fr 1fr;
  gap: 100px;
}
.profilep {
  display: flex;
  flex-direction: column;
  color: #fff;
  width: 70vw;
  height: auto;
  border-radius: 20px;
  padding: 30px;
  margin: 30px;
  background-color: rgba(60, 57, 57, 0.527);
}
.booking-details {
  background: #fff;
  background-color: rgba(51, 51, 51, 0.324);
  backdrop-filter: blur(20px);
  border-radius: 4px;
  margin: 20px auto;
  padding: 20px;
  position: relative;
  box-shadow: 5px 2px 5px rgba(0,0,0,0.05);
}

@media screen and (max-width: 1200px) {
  .home {
    display: flex;
    flex-direction: column;
    padding: 2rem;
  }
  .react-calendar { 
    width: 400px;
    max-width: 100%;
    background-color: rgb(45, 94, 61);
    color: rgb(255, 255, 255) !important;
    border-radius: 8px;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.125em;
    position: relative;
    left: 1%;
   }
}

.booking-details h4 {
  margin: 0 0 10px 0;
  font-size: 1.2em;
  color: var(--primary);
}
.booking-details p {
  margin: 0;
  font-size: 0.9em;
  color: rgb(255, 255, 255);
}
.booking-details span {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  background: #f1f1f1;
  padding: 6px;
  border-radius: 50%;
  color: #333;
}

/* new booking form */ 

label{
  display: block;
  color: #ddd;
}

form h2 {
  color: #f1f1f1;
}

input {
  display: block;
  color: rgb(0, 0, 0);
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}
 button {
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 10px;
  font-family: "Poppins";
  border-radius: 4px;
  cursor: pointer;
}
div.error {
  padding: 10px;
  background: #ffefef;
  border: 1px solid var(--error);
  color: var(--error);
  border-radius: 4px;
  margin: 20px 0;
}
input.error {
  border: 1px solid var(--error);
}
.casual{
  color: rgb(255, 255, 255) !important;
  background-color: rgba(51, 51, 51, 0) !important;
  backdrop-filter: blur(20px);
}

.casual1{
  position: relative !important; 
  cursor: pointer;
  left: 95% !important;
  color: rgb(255, 255, 255) !important;
  background-color: rgba(51, 51, 51, 0) !important;
  backdrop-filter: blur(20px);
}


/* navbar */
nav {
  display: flex;
  align-items: center;
}
nav a, nav button {
  margin-left: 10px;
}
nav button {
  background: #fff;
  color: var(--primary);
  border: 2px solid var(--primary);
  padding: 6px 10px;
  border-radius: 4px;
  font-family: "Poppins";
  cursor: pointer;
  font-size: 1em;
}

/* mobile nav bar view  */

@media screen and (max-width: 760px) {
  nav {
    display: none;
  }
}

/* auth forms */
form.signup, form.login {
  background-color: rgba(51, 51, 51, 0) !important;
  backdrop-filter: blur(20px);
  max-width: 400px;
  margin: 40px auto;
  padding: 20px;
  background: #fff;
  border-radius: 4px;
}

form h3{
  color: #fff !important;
}

/* homepage css  */

.mainlayoutsec{
  height: 100vh;
  width: 100%;
  color: azure;
}
.mainlayoutsec h1 {
  text-align: center;
  position: relative;
  top: 20vh;
  font-size: 4.5rem;
}
.mainlayoutsec button {
  position: relative;
  font-family: Cambria;
  font-size: 1.2rem;
  background:rgb(255, 255, 255);
  border-radius: 10px;
  border: none;
  left: 43%;
  top: 15vh;
  width: 12vw;
  height: 10vh;
}

.mainlayoutsec button:hover{
  position: relative;
  font-family: Cambria;
  font-size: 1.5rem;
  background:#000000;
  color: white;
  border-radius: 20px;
  border: none;
  left: 43%;
  top: 14vh;
  width: 13vw;
  height: 11vh;
}

/* footer css */

.footertag{
  display: flex;
  background-color: rgb(0, 0, 0);
  padding: 2rem;
  flex-direction: column;
  height: 25vh;
  width: 100%;
}
.mainfootersec{
  display: flex;
  color: aliceblue;
}
.sec1{
  width: 33%;
}
.sec1 p{
  width: 70%;
}
.sec2{
  width: 33%;
}
/* phone view footer  */

@media screen and (max-width: 550px) {
  .footertag{
    display: flex;
    flex-direction: column;
    background-color: rgb(0, 0, 0);
    padding: 2rem;
    flex-direction: column;
    height: 25vh;
  }
  .mainfootersec{
    display: flex;
    flex-direction: column;
    color: aliceblue;
  }
  .sec1{
    width: 100%;
  }
  .sec1 p{
    width: 100%;
  }
  .sec2{
    width: 100%;
  }
}

/* Layout main  */

.mainlayoutt {
  display: flex;
  justify-content: space-around;
  color: rgb(239, 239, 239);
  height: auto;
  width: 3fr;
  background-color: rgb(81, 81, 81);
  margin: 20px auto;
  padding: 20px;
  position: relative;
  box-shadow: 5px 2px 5px rgba(0,0,0,0.05);
  backdrop-filter: blur(20px);
  border-radius: 10px;
}

/* calender css */

.react-calendar { 
  width: 400px;
  max-width: 100%;
  background-color: rgb(45, 94, 61);
  color: rgb(255, 255, 255) !important;
  border-radius: 8px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
  position: relative;
  left: 45%;
 }

 .react-calendar button{ 
  background-color: #76d399;
  color: #000000;
 }

 .react-calendar__navigation button {
  color: #f0f0f0;
  min-width: 44px;
  background: rgb(28, 90, 55) !important;
  font-size: 16px;
  margin-top: 8px;
 }
 .react-calendar__navigation button:enabled:hover,
 .react-calendar__navigation button:enabled:focus {
  background-color: #f8f8fa;
 }
 .react-calendar__navigation button[disabled] {
  background-color: #f0f0f0;
 }
 abbr[title] {
  text-decoration: none;
 }
 /* .react-calendar__month-view__days__day--weekend {
  color: #d10000;
 } */
 .react-calendar__tile:enabled:hover,
 .react-calendar__tile:enabled:focus {
  background: #f8f8fa;
  color: #1b5b43;
  border-radius: 6px;
 }
 .react-calendar__tile--now {
  background: #ffffff;
  border-radius: 6px;
  font-weight: bold;
  color: #1b5b43;
 }
 .react-calendar__tile--now:enabled:hover,
 .react-calendar__tile--now:enabled:focus {
  background: #ffffff;
  border-radius: 6px;
  font-weight: bold;
  color: #1b5b43;
 }
 .react-calendar__tile--hasActive:enabled:hover,
 .react-calendar__tile--hasActive:enabled:focus {
  background: #f8f8fa;
 }
 .react-calendar__tile--active {
  background: #1b5b43;
  border-radius: 6px;
  font-weight: bold;
  color: white;
 }
 .react-calendar__tile--active:enabled:hover,
 .react-calendar__tile--active:enabled:focus {
  background: #1b5b43;
  color: white;
 }
 .react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #f8f8fa;
 }
 .react-calendar__tile--range {
  background: #f8f8fa;
  color: #1b5b43;
  border-radius: 0;
 }
 .react-calendar__tile--rangeStart {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  background: #1b5b43;
  color: white;
 }
 .react-calendar__tile--rangeEnd {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  background: #1b5b43;
  color: white;
 }

 .hours{
  position: relative;
  left: 20%;
 }



